import React, { Component, useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Input,
  Typography,
  Badge,
  Divider,
  Tag,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "./hotlineMessages.scss";
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import Messages from "./Messages";
import { BASE_URL } from "../../userconfig";
import { Scrollbars } from "react-custom-scrollbars";
import * as AppStorage from "../../utils/AppStorage";
import NoHotlines from "../../assets/images/no-hotlines.svg";
import CreateHotline from "../../assets/images/create-hotline.svg";

const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;

const HotlineMessages = () => {
  // constructor() {
  //   super();
  //   this.state = {
  //     conversations: [],
  //     conversation: "",
  //     urgencyLevel: "",
  //     status: "",
  //     searchString: "",
  //   };
  //   this.onConversationClick = this.onConversationClick.bind(this);
  //   this.onSearch = this.onSearch.bind(this);
  // }
  const [conversations, setConversations] = useState([]);
  const [initialConv, setInitialConv] = useState([]);
  const [urgencyLevelData, setUrgencyLevelData] = useState([]);
  const [urgencyLevelHotlines, setUrgencyLevelHotline] = useState([]);
  const [urgencyLevelStatus, setUrgencyLevelStatus] = useState([]);
  const [removeFilter, setRemoveFilter] = useState({});
  const [conversation, setConversation] = useState("");
  const [urgencyLevel, setUrgencyLevel] = useState("");
  const [status, setStatus] = useState("");
  const [searchString, setSearchString] = useState("");

  const [form] = Form.useForm();

  useEffect(() => {
    getConversations();
  }, []);

  function arrayUnique(arr, urgencyLevels, status, hotline) {
    setUrgencyLevelData(urgencyLevels);
    setUrgencyLevelStatus(status);
    setUrgencyLevelHotline(hotline);
  }

  const loadFilteredConversations = (
    data,
    type,
    selectedUrgency,
    selectedStatus,
    selectedHotline
  ) => {
    const dataValue = arrayUnique(
      data,
      selectedUrgency,
      selectedStatus,
      selectedHotline
    );
    setConversations(data);
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
  };

  function handleFilterLevel(type, value) {
    setRemoveFilter({ type, value });
  }

  const getConversations = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (user)
      axios
        .get(
          BASE_URL +
            "hotlineResponderConversation/?user_id=" +
            user.user_details.org_user_id,
          options
        )
        .then((res) => {
          setInitialConv(res.data);
          setConversations(res.data);
        });
  };

  const onConversationClick = (conversation) => {
    setConversation(conversation);
    setUrgencyLevel(conversation.urgency_level.urgency_level_id);
    setStatus(conversation.status.status_id);
  };

  const onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setUrgencyLevel(event);
        }
      });
  };

  const onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      status_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setStatus(event);
        }
      });
  };

  const onSearch = (e) => {
    const statusFil = urgencyLevelStatus.map((status) => {
      status = status.value;
      return status;
    });
    let allCheckedOnes = {
      ...(statusFil.length > 0 && { status_id: statusFil }),
      ...(urgencyLevelData.length > 0 && {
        urgency_level_id: urgencyLevelData.map((ugc) => ugc.value),
      }),
      ...(urgencyLevelHotlines.length > 0 && {
        hotline_id: urgencyLevelHotlines.map((ht) => ht.value),
      }),
    };
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;
    const finalUrl =
      e === ""
        ? baseUrl + "&filter=" + JSON.stringify(allCheckedOnes)
        : baseUrl +
          "&filter=" +
          JSON.stringify(allCheckedOnes) +
          "&search=" +
          e.toString();
    axios.get(finalUrl, options).then((res) => {
      setConversations(res.data);
      setSearchString(e);
    });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amWEBUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AmWebSidebar amWEBUser />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">Hotline Messages</h1>
              </Row>
              {initialConv?.length > 0 ? (
                <div className="mid-content p-0">
                  <Row>
                    <Col span={6} className="message-section py-4">
                      <Col
                        span={24}
                        className=" d-flex justify-content-between px-3"
                      >
                        <h6>Messages</h6>

                        <FilterPopover
                          searchString={searchString}
                          removeFilter={removeFilter}
                          loadFilteredConversations={loadFilteredConversations}
                        />
                      </Col>

                      <Col span={24} className="mt-4 px-3 d-block">
                        <Scrollbars
                          // onScroll={handleScroll}
                          // onScrollFrame={handleScrollFrame}
                          // onScrollStart={handleScrollStart}
                          // onScrollStop={handleScrollStop}
                          // onUpdate={handleUpdate}
                          // renderView={renderView}
                          // renderTrackHorizontal={renderTrackHorizontal}
                          // renderTrackVertical={renderTrackVertical}
                          // renderThumbHorizontal={renderThumbHorizontal}
                          // renderThumbVertical={renderThumbVertical}
                          autoHeight
                          autoHeightMin={0}
                          autoHeightMax={150}
                          thumbMinSize={30}
                          universal={true}
                        >
                          <>
                            {urgencyLevelData.map((data, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  key={index}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyLevel",
                                      data.value
                                    )
                                  }
                                  className="mb-2"
                                >
                                  Urgency: <span>{data.label}</span>
                                </Tag>
                              </>
                            ))}
                            {urgencyLevelHotlines.map((hotline, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyHotline",
                                      hotline.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Hotline: <span>{hotline.label} </span>
                                </Tag>
                              </>
                            ))}
                            {urgencyLevelStatus.map((status, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyStatus",
                                      status.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Status: <span>{status.label} </span>
                                </Tag>
                              </>
                            ))}
                          </>
                        </Scrollbars>
                      </Col>
                      <Col span={24} className="search-box mt-3 px-3">
                        <Form form={form}>
                          <Form.Item name="search" id="search">
                            <Search
                              name="search"
                              id="search"
                              placeholder="Search"
                              onChange={(e) => onSearch(e.target.value)}
                              onSearch={onSearch}
                            />
                          </Form.Item>
                        </Form>
                      </Col>
                      <Scrollbars
                        // onScroll={handleScroll}
                        // onScrollFrame={handleScrollFrame}
                        // onScrollStart={handleScrollStart}
                        // onScrollStop={handleScrollStop}
                        // onUpdate={handleUpdate}
                        // renderView={renderView}
                        // renderTrackHorizontal={renderTrackHorizontal}
                        // renderTrackVertical={renderTrackVertical}
                        // renderThumbHorizontal={renderThumbHorizontal}
                        // renderThumbVertical={renderThumbVertical}
                        autoHeight
                        autoHeightMin={0}
                        autoHeightMax={450}
                        thumbMinSize={30}
                        universal={true}
                      >
                        <Col span={24} className="mt-4 inbox-messages">
                          {conversations.length > 0 ? (
                            conversations.map((item) => (
                              <div
                                className={
                                  conversation.conversation_name ===
                                  item.conversation_name
                                    ? "msg-wrapper active"
                                    : "msg-wrapper"
                                }
                                onClick={() => onConversationClick(item)}
                              >
                                <div className="d-flex justify-content-between">
                                  <Paragraph
                                    ellipsis
                                    className="w-75 pr-2 mb-1"
                                  >
                                    {item.responder_conversation_name}
                                  </Paragraph>

                                  <span className="small">
                                    {" "}
                                    {moment(item.created_on).isSame(
                                      moment(),
                                      "day"
                                    )
                                      ? moment(item.created_on).utc().local().format("hh:mm")
                                      : moment(item.created_on).utc().local().format("MM/DD/YY")}
                                  </span>
                                </div>
                                <div className="d-flex justify-content-between">
                                  <Paragraph
                                    ellipsis
                                    className="font-weight-normal mb-1 sm"
                                  >
                                    {item.hotline.name} | {item.conversation_id}
                                  </Paragraph>
                                  <span className="small">
                                    {" "}
                                    <Badge count={item.unread_count} />
                                  </span>
                                </div>
                              </div>
                            ))
                          ) : (
                            <div className="msg-wrapper">No messages found</div>
                          )}
                        </Col>
                      </Scrollbars>
                    </Col>

                    {conversation ? (
                      <Messages
                        conversation={conversation}
                        urgencyLevel={urgencyLevel}
                        status={status}
                        onChangeUrgencyLevel={onChangeUrgencyLevel}
                        onChangeStatus={onChangeStatus}
                      />
                    ) : (
                      <Col
                        span={18}
                        className="chat-section no-selected bg-white justify-content-center align-items-center d-flex"
                      >
                        <Row className="justify-content-center my-3">
                          <img
                            src={NoHotlines}
                            alt="No Hotlines Selected"
                            style={{ opacity: "0.8" }}
                          />
                        </Row>
                        <h5>No Hotline Selected</h5>
                        <small>Select a hotline to view</small>
                      </Col>
                    )}
                  </Row>
                </div>
              ) : (
                <Col
                  span={24}
                  className="chat-section no-selected bg-white justify-content-center align-items-center d-flex"
                >
                  <Row className="justify-content-center my-3">
                    <img
                      src={CreateHotline}
                      alt="No Hotlines Selected"
                      style={{ opacity: "0.8" }}
                    />
                  </Row>
                  <h5>
                    No conversations are initiated for the hotlines assigned to
                    you
                  </h5>
                  {/* <small>Select a hotline to view</small> */}
                </Col>
              )}
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default HotlineMessages;
